<template>
  <div>
    <button @click="toggleCodeMirrorMode">tootle mode</button>
    <button @click="insertScript">Insert script</button>
    <input type="text" v-model="scriptText">
    <textarea v-model="content" @change="changeText" id="edit"></textarea>
  </div>
</template>

<script>
  import * as CodeMirror from 'codemirror'
  import 'codemirror/lib/codemirror.css'
  import 'codemirror/theme/dracula.css'
  import 'codemirror/mode/javascript/javascript.js'
  import 'codemirror/mode/gfm/gfm.js'
import { javascript } from '@codemirror/lang-javascript'


  export default {
    name: 'EditCode',
    data() {
      return {
        content: 'console.log(3)',
        scriptText: '',
        modes: [
          'gfm',
          'javascript'
        ]
      }
    },
    methods: {
      changeText(val) {
        console.log('code text::', val)
      },
      toggleCodeMirrorMode() {
        const codeMirrorMode = this.cm.getOption('mode');
        if (codeMirrorMode === 'javascript') {
          this.cm.setOption('mode','gfm')
        } else{
          this.cm.setOption('mode','javascript')

        }
      },
      insertScript() {
        if (!this.scriptText) return;
        this.insert(this.scriptText)
      },
      insert(val) {
        let pos1 = this.cm.getCursor();
        let pos2 = {};
        pos2.line = pos1.line;
        pos2.ch = pos1.ch;
        this.cm.replaceRange(val,pos2)
      }
    },
    mounted() {
    window.cm = this.cm = CodeMirror.fromTextArea(
        document.getElementById('edit'),
        {
          // lineNumbers: true,
          theme: 'dracula',
          // smartIndent: true,
          mode: 'javascript',
          tabSize: 2,
        // 主题，对应主题库 JS 需要提前引入
        // theme: 'cobalt',
        // 显示行号
        lineNumbers: true,
        lineSeparator: '~',
        line: true,
        lineWrapping: true,  // 自动换行
        styleActiveLine: true, // 当前行背景高亮
        smartIndent: true,
        indentUnit: 1,  // 缩进单位为4
        extraKeys: { Ctrl: "autocomplete" }, //避免热键冲突
        styleActiveLine: true
        }
      )
    }
  }
</script>