var checkAge = (rule, value, callback) => {
    if (!value) {
        return callback(new Error('年龄不能为空'));
    }
    setTimeout(() => {
        if (!Number.isInteger(value)) {
            callback(new Error('请输入数字值'));
        } else {
            if (value < 18) {
                callback(new Error('必须年满18岁'));
            } else {
                callback();
            }
        }
    }, 1000);
};
var validatePass = (rule, value, callback) => {
    if (value === '') {
        callback(new Error('请输入密码'));
    } else {
        if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
        }
        callback();
    }
};
var validatePass2 = (rule, value, callback) => {
    if (value === '') {
        callback(new Error('请再次输入密码'));
    } else if (value !== this.ruleForm2.pass) {
        callback(new Error('两次输入密码不一致!'));
    } else {
        callback();
    }
};


const generateUnionRuleObj = function (cFn, positiveChain, negativeChain) {
    return {
        required: true,
        validator: async (rule, value, callback) => {
           
            /**
             * 
             * @param {ruleObj} item 正则ruleObj对象或者自定义 ruleObj
             */
            const wrappedFn = item => {
                if (item.validatorFn && typeof item.validatorFn === 'function') {
                    const validResult = item.validatorFn(item, value);
                    const result = {};
                    result.pass = typeof validResult === 'boolean' ? validResult : validResult.pass;
                    result.message = typeof validResult === 'boolean' && item.message ? item.message : validResult.message;
                    return result;
                }
                if (item.pattern) {
                    const result = {};
                    result.pass = item.pattern.test(value);
                    result.message = item.message;
                    return result;
                }
                return {
                    pass: true,
                };
            }
            let fnChain = null;
            const conditionResult = await cFn.apply(arguments, arguments)
            if (conditionResult) {
                message = 'please select';
                fnChain = positiveChain.map(item => () => wrappedFn(item));
            } else {
                fnChain = negativeChain.map(item => () => wrappedFn(item));
            }
            if (!fnChain) {
                callback();
                return;
            }
        
            for (const fn of fnChain) {
                const result = fn();

               let message = result.message || '';
                if (result === true) {
                    // callback();
                } else if (!result.pass) {
                    callback(new Error(message))
                    return;
                } else {
                    // callback();
                }
            }
        },
    };
};


